<template>
  <div class="page-login">
    <!-- <div class="load">
		  <img src="@/assets/images/mentos.gif" alt="">
	  </div> -->
    <div class="layout">
      <header-com :index="-1" :scrollTop="scrollTop"></header-com>
      <div class="flogin">
        <div class="wrap">
          <div
            class="fpitl flog wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="1s"
          >
            <h3>Log in</h3>
            <i></i>
          </div>
          <div
		  v-if="isLogin"
            class="floginvia wow fadeInUp"
            data-wow-delay="0.3s"
            data-wow-duration="1s"
          >
            <div class="ficon">
              <img src="@/assets/images/fico19.png" alt="" />
            </div>
            <div class="teitl">
              <h2 :class="isEmail ? 'on' : ''" @click="onEmail">
                Login via Email
              </h2>
              <h2 :class="!isEmail ? 'on' : ''" @click="onAccount">
                Login via Discord
              </h2>
            </div>
            <div>
              <!-- <el-button type="primary"></el-button> -->
            </div>
            <div v-show="isEmail" class="femail">
              <div class="femil">
                <form action="" method="post">
                  <div class="list">
                    <ul>
                      <li>
                        <label>Email</label>
                        <input
                          type="text"
                          class="text"
                          v-model="email"
                          placeholder="Enter your email"
                        />
                      </li>
                      <li>
                        <label>Password</label>
                        <input
                          type="text"
                          v-model="password"
                          class="text"
                          placeholder="Password"
                        />
                      </li>
					  
                    </ul>
                  </div>
				  <div class="img-code">
					<input
                          type="text"
                          v-model="imgCode"
                          class="text"
                          placeholder="Picture content"
                        />
					  <img :src="captcha_image_base" alt="">
					  <div @click="getCatchCode" style="cursor:pointer">invisibility</div>
				  </div>
                  <div class="forget">
                    <span  @click="forgetPasswordShow">Forget password</span>
                  </div>
                  <div @click="submit" class="sub">login</div>

                  <router-link to="/createAccount" class="fcreate"
                    ><i>Create an account</i></router-link
                  >
                </form>
              </div>
            </div>
            <div v-show="!isEmail" class="femail">
              <div class="femil">
                <form action="" method="post">
                  <div class="list">
                    <ul>
                      <li>
                        <label>Name</label>
                        <input
                          type="text"
                          class="text"
                          v-model="name"
                          placeholder="Enter your name"
                        />
                      </li>
                      <li>
                        <label>Password</label>
                        <input
                          type="text"
                          v-model="password"
                          class="text"
                          placeholder="Password"
                        />
                      </li>
                    </ul>
                  </div>
                  <div class="img-code">
					<input
                          type="text"
                          v-model="imgCode"
                          class="text"
                          placeholder="Picture content"
                        />
					  <img :src="captcha_image_base" alt="">
					  <div @click="getCatchCode" style="cursor:pointer">invisibility</div>
				  </div>
                  <div class="forget">
                    <span  @click="forgetPasswordShow">Forget password</span>
                  </div>
                  <!-- <input type="submit" class="sub" @click="submit" value="login" /> -->
                  <div @click="submit" class="sub">login</div>
                  <div @click="toUrl" class="fcreate">
                    <i>Create an account</i>
                  </div>
                </form>
              </div>
            </div>
          </div>

		  <!--  -->
		  <div
		    v-if="forgetFlag"
            class="floginvia wow fadeInUp forget-bottom"
            data-wow-delay="0.3s"
            data-wow-duration="1s"
          >
            <div class="ficon">
              <img src="@/assets/images/fico19.png" alt="" />
            </div>
            <div class="teitl">
              <h2 class="" style="width:100%;font-size: 22px;color: #404040">
                Password reset
              </h2>
            </div>
            <div style="text-align:center;font-weight:600;font-size: 12px">
				Please enter your email.We' ll send you a code to your email. 
            </div>
            <div class="femail">
			  <div class="form-box">
                <el-form ref="form" :model="form" label-width="100px">
                  <el-form-item label="email">
                    <el-input
                      v-model="form.email"
                      placeholder="email"
                    ></el-input>
                  </el-form-item>
                  <el-form-item label="code">
                    <el-input
                      v-model="form.email_code"
                      placeholder="code"
                    ></el-input>
                  </el-form-item>
                  
                </el-form>
                <div class="send-code" @click="getCode">
                  send code 
                </div>
              </div>
			  <div @click="subForget" class="subForget">Next</div>
			  <div style="padding-top:40px"></div>
            </div>
          </div>

		   <!--  -->
		   <div
		    v-if="resetFlag"
            class="floginvia wow fadeInUp forget-bottom"
            data-wow-delay="0.3s"
            data-wow-duration="1s"
          >
            <div class="ficon">
              <img src="@/assets/images/fico19.png" alt="" />
            </div>
            <div class="teitl">
              <h2 class="" style="width:100%;font-size: 22px;color: #404040">
                Password reset
              </h2>
            </div>
            <div style="text-align:center;font-weight:600;font-size: 12px">
				Please enter your new password to reset it.
            </div>
            <div class="femail">
			  <div class="form-box">
                <el-form ref="form-reset" :model="formReset" label-width="140px">
                  <el-form-item label="new Password">
                    <el-input
                      v-model="formReset.password"
                      placeholder="password"
                    ></el-input>
                  </el-form-item>
                  <el-form-item label="re-Password">
                    <el-input
                      v-model="formReset.newPassword"
                      placeholder="newPassword"
                    ></el-input>
                  </el-form-item>
                  
                </el-form>
              </div>
			  <div @click="resetPaaword" class="subForget">confirm</div>
			  <div style="padding-top:40px"></div>
            </div>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>
  
  <script >
import headerCom from "@/components/header";
import Cookies from "js-cookie";
  import {  login,sendCodeForget,loginOut,verifyMailbox,resetPaasord,catchVerificationCode } from '@/api/login'
// import { login } from "../api/login";

export default {
  name: "login",
  components: {
    headerCom,
  },
  data() {
    return {
      scrollTop: 0,
      password: "",
      email: "",
	  imgCode: '',
      name: "",
      isEmail: 1,
	  isLogin: true,
	  resetFlag: false,
	  forgetFlag: false,
	  email_code_id: null,
	  verify_id: null,
	  captcha_image_base: '',
	  form: {
        email: "",
        email_code: '',
      },
	  formReset: {
		password: '',
		newPassword: '',
	  }
    };
  },
  updated() {},
  mounted() {
    new this.$wow.WOW().init();
    window.addEventListener("scroll", this.handleScroll);
	this.getCatchCode();
  },
  methods: {
	
	  reSend() {
		this.getCatchCode()
	  },
	resetPaaword() {
		if(this.formReset.password.length && this.formReset.password === this.formReset.newPassword) {
			let params = {
        email:this.form.email,
		password: this.formReset.password,
		verify_id: this.verify_id,
      };
	  resetPaasord(params,(res) => {
		  this.verify_id = null;
		  this.email_code_id =null
		  this.resetFlag = false
		  this.isLogin = true
		  this.forgetFlag = false
		  this.form.email = '';
		  this.form.email_code = '';
		  this.formReset.password = '';
		  this.formReset.newPassword = '';
                  }, (err) => { this.$message.warning(err.msg); })
			
		} else if(this.formReset.password.length == 0 || this.formReset.newPassword.length == 0){
			this.$message.warning('Please enter your password!');
			return
		} else if(this.formReset.password !== this.formReset.newPassword) {
			this.$message.warning('The two passwords are inconsistent!');
			return
		}
		
	},
	subForget() {
		if(this.form.email.length == 0 || this.form.email_code.length == 0) {
			this.$message.warning('Please enter your email or code!');
			return
		}
		let params = {
        email:this.form.email,
		email_code: this.form.email_code,
		email_code_id: this.email_code_id,
      };
	  verifyMailbox(params,(res) => {
		  console.log(res)
		  this.verify_id = res.data.verify_id;
		  this.resetFlag = true
		  this.forgetFlag = false
                  }, (err) => { this.$message.warning(err.msg); })
		
	},
	getCode() {
      let params = {
        email:this.form.email,
		check_user: 1,
		type: 'forgot',
      };
      if(!params.email || params.email.length==0) {
        this.$message.warning('Please enter your email address!');
      }
	  sendCodeForget(params,(res) => {
		  this.email_code_id = res.data.email_code_id;
        this.$message.success('Sending succeeded')
                  }, (err) => { this.$message.warning(err.msg); })
      
    },
	forgetPasswordShow() {
	  loginOut(res => {
		  console.log(res)
		this.isLogin = false
		this.forgetFlag = true
                  }, (err) => { this.$message.warning(err.msg); })
	},
    // forgetPasswordShow() {
		
		
	// },
    toUrl() {
      this.$router.push({ path: "/createAccount" });
    },
    handleScroll() {
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop; // 
      this.scrollTop = scrollTop;
    },
    onAccount() {
      this.isEmail = false;
      this.email = "";
    },
    onEmail() {
      this.isEmail = 1;
      this.name = "";
    }, 
	getCatchCode() {
		catchVerificationCode(res => {
			console.log(res)
			this.captcha_image_base = res.data.captcha_image_base
			this.captcha_id = res.data.captcha_id
                  }, (err) => { this.$message.warning(err.msg); })
	},

    submit() {
		if(this.name.length == 0 && this.email.length == 0) {
			this.$message.warning('Please enter your name or email!');
			return
		}
		if(this.password.length == 0) {
			this.$message.warning('Please enter your password!');
			return
		}
		if(this.imgCode.length == 0) {
			this.$message.warning('Please enter a picture result!');
			return
		}
		this.submitSend()
    },
	submitSend() {
		let params = {
        username: this.name.length ? this.name : this.email,
        password: this.password,
        captcha_id: this.captcha_id,
        captcha: this.imgCode,
      };
      login(
        params,
        (res) => {
			console.log(res.data)
			this.$store.commit('setUserInfo', res.data);
          this.$message.success("Login succeeded");
          this.$router.push({ path: "/product" });
          // this.$router.push({path:'/dashboard'})
        },
        (err) => {
          this.$message.warning(err.msg);
        }
      );

	}
  },
};
</script>
  <!-- <style scoped >
	
  </style> -->
  <style lang="scss" scoped>
.layout {
  background: url(../assets/images/fbj1.jpg) no-repeat center/cover;
}
.floginvia {
  background: url(../assets/images/fimg5.jpg) no-repeat center/cover;
}
.forget-bottom {
	width: 540px!important;
}
.forget {
	text-align: right;
	span {
		cursor: pointer;
	}
}
.sub {
	text-align: center;
	line-height: 48px;
}
.page-login {
  .femil {
    padding: 20px;
    @include themeify {
      background-color: themed("bg-color");
    }
  }
}
.form-box {
    position: relative;
    display: flex;
    justify-content: space-between;
    .el-form {
      width: 380px;
      
    }
    .send-code {
      margin-top: 5px;
      margin-left: 20px;
      background: #C76092;
      color: #000;
      height: 30px;
      line-height: 30px;
      border-radius: 8px;
      text-align: center;
      padding: 0px 10px;
      cursor: pointer;
    }
  }
  .img-code {
	  display: flex;
	  justify-content: space-around;
	  align-items: center;
	  height: 100px;
	  input {
		  outline: none;
		  background: #F7F7F7;
		  border: none!important;
		  line-height: 40px;
		  text-align: left;
		  text-indent: 10px;
		  font-weight: 600;
		  font-size: 12px;
		//   border: 1px solid ;
		  

	  }
	  img {
		  width: 80px;
		  height: 40px;

	  }
	  div {
		  width: 100px;
		  line-height: 34px;
		  background: #C76092;
		  color: #000;
		  font-weight: 600;
		  font-size: 12px;
		  text-align: center;
		  border-radius: 10px;


	  }
  }

	.subForget {
		margin: 0 auto;
		width: 200px;
		text-align: center;
		background: #C76092;
      color: #000;
	  margin-top: 20px;
	  height: 50px;
	  font-size: 14px;
      line-height: 50px;
	  cursor: pointer;
	  border-radius: 20px;
	}
.fcreate {
  cursor: pointer;
}
</style>