<template>
  <div class="page-login">
    <!-- <div class="load">
		<img src="@/assets/images/mentos.gif" alt="">
	</div> -->
    <div class="layout">
      <header-com :index="-1" :scrollTop="scrollTop"></header-com>
      <div class="flogin">
        <div class="wrap">
          <div
            class="fpitl flog wow fadeInUp"
            data-wow-delay="0.2s"
            data-wow-duration="1s"
          >
            <h3>Log in</h3>
            <i></i>
          </div>
          <div
            class="floginvia wow fadeInUp"
            data-wow-delay="0.3s"
            data-wow-duration="1s"
          >
            <div class="ficon">
              <img src="@/assets/images/fico19.png" alt="" />
            </div>
            <div class="femail">
              <div class="form-box">
                <el-form ref="form" :model="form" label-width="120px">
                  <el-form-item label="username">
                    <el-input
                      v-model="form.username"
                      placeholder="username"
                    ></el-input>
                  </el-form-item>
                  <el-form-item label="email">
                    <el-input
                      v-model="form.email"
                      placeholder="email"
                    ></el-input>
                  </el-form-item>
                  <el-form-item label="code">
                    <el-input
                      v-model="form.email_code"
                      placeholder="code"
                    ></el-input>
                  </el-form-item>
                  <el-form-item label="password">
                    <el-input v-model="form.password"></el-input>
                  </el-form-item>
                  <el-form-item label="re-Password">
                    <el-input v-model="form.rePassword"></el-input>
                  </el-form-item>
                  <el-form-item label="invite code">
                    <el-input v-model="form.invite_code"></el-input>
                  </el-form-item>
                  
                </el-form>
                <div class="send-code" @click="getCode">
                  send code 
                </div>
              </div>
              <div class="operate">
                    <div class="button" @click="onSubmit"
                      >sign up</div
                    >
                    <div @click="toLogin" class="button">sign in</div>
                  </div>
              <div class="femil">
                <form action="" method="post">
                  <div class="fimgs">
                    <img src="@/assets/images/fico20.png" alt="" />
                  </div>
                  <input
                    @click="submit()"
                    type="submit"
                    class="sub"
                    value="login"
                  />
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script >
import headerCom from "@/components/header";
import {  createAccount,sendCode,login } from '@/api/login'
export default {
  name: "login",
  components: {
    headerCom,
  },
  data() {
    return {
      scrollTop: 0,
      form: {
        username: "",
        password: "",
        email: "",
        invite_code: "aadd",
        email_code: '',
        discord_id: 11111,
      },
    };
  },
  updated() {},
  mounted() {
    new this.$wow.WOW().init();
    window.addEventListener("scroll", this.handleScroll);
  },
  methods: {
    onSubmit() {
      let keys = Object.keys(this.form)
      let flag = keys.every(ele => {
        let isData = false;
        if(this.form[ele] && this.form[ele].length) {
          isData = true
        }
        if(ele =='discord_id') {
          isData = true
        }
        return isData
      })
      if(!flag) {
        this.$message.warning('Please complete the form first!');
        return
      }
      let params = this.form
      createAccount(params,(res) => {
        this.$message.success('Created successfully');
        this.$router.push({path: '/login'});
                  }, (err) => { this.$message.warning(err.msg); })
    },
    getCode() {
    //   login({"username": "yang",
    // "password": "123",
    // "code": "1234",
    // "captcha_id":"121313",
    // "captcha":"aadv"},(res) => {
    //     this.$message.success('Login succeeded')
		// this.$router.push({path: '/product'});
    //               }, (err) => { this.$message.warning(err.msg); })
      let params = {
        email:this.form.email,
        type: 'register',
      };
      if(!params.email || params.email.length==0) {
        this.$message.warning('Please enter your email address!');
      }
       sendCode(params,(res) => {
         console.log(res)
        this.$message.success('Sending succeeded')
                  }, (err) => { this.$message.warning(err.msg); })
      
    },
    
    handleScroll() {
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop; // 
      this.scrollTop = scrollTop;
    },
    onAccount() {
      this.isEmail = false;
    },
    toLogin() {
      this.$router.push({path: '/login'});
    }
  },
};
</script>
