  background: url(../assets/images/fimg5.jpg) no-repeat center/cover;
}

.page-login {
  ::v-deep .el-form-item__label {
  text-align: left;
  font-weight: 600;
}
::v-deep .floginvia {
  width: 600px;
}
 
  .form-box {
    padding: 20px;
    @include themeify {
      background-color: themed("bg-color");
    }
  }
  .operate {
        display: flex;
        justify-content: space-around;
        .button {
      background: #C76092;
      color: #000;
      height: 40px;
      font-size: 14px;
      font-weight: 600;
      line-height: 40px;
      border-radius: 12px;
      text-align: center;
      padding: 0px 20px;
      cursor: pointer;
        }
      }
  .form-box {
    position: relative;
    display: flex;
    justify-content: flex-start;
    .el-form {
      width: 400px;
      
    }
    .send-code {
      margin-top: 68px;
      margin-left: 20px;
      background: #C76092;
      color: #000;
      height: 30px;
      line-height: 30px;
      border-radius: 8px;
      text-align: center;
      padding: 0px 10px;
      cursor: pointer;
    }
  }
}
