<template>
  <div class="">
    <div class="layout">
		<header-com :index="0" :scrollTop="scrollTop"></header-com>
		<div class="fcokeprox">
			<div class="wrap">
				<div class="fcokeimgs wow fadeInUp" data-wow-delay="0.3s" data-wow-duration="1s">
					<div class="imgs">
						<img src="@/assets/images/fico9.png" alt="">
					</div>
					<div class="chi"><img src="@/assets/images/fico7.png" alt=""></div>
					<div class="fnbt" @click="scroll_pro"><img src="@/assets/images/fico8.png" alt=""></div>
					<div class="ftlt">
						 <swiper :options="swiperOption" ref="mySwiper" class="swipertab">
								<swiper-slide v-for="(item, index) in labelListData" :key="index" >
										<div class="ftlt_li" @click="toProduct(item.id)"><span>{{item.labelName}}</span></div>
								</swiper-slide>
						 </swiper>
					</div>
				</div>
				
				<div class="fpickthe" id="fpickthe">
					<form action="" method="post">
						<div class="fpitl wow fadeInUp" data-wow-delay="0.2s" data-wow-duration="1s">
							<h3>Pick the best product for you</h3>
							<i></i>
						</div>
						<div class="list wow fadeInUp" data-wow-delay="0.3s" data-wow-duration="1s">
							<ul>
								<li class="ftobor">
									<h2>Select the function you need:</h2>
									<div class="select">
										<dl>
											<dd v-for="(item, index) in labelListData" :key="index">
												<label :class="item.choose?'active_label':''" @click="chooseLable(index)">{{item.labelName}}</label>
											</dd>
										</dl>
									</div>
									<div class="jianto" v-if="chooseId!==''"></div>
								</li>
								<li class="ftobor ftobor2 index_choose_type" v-if="chooseId!==''">
									<img src="@/assets/images/fimg2.jpg" alt="" class="index_choose_type_bg"/>
									 <div class="fmore" @click="chooseMoreBtn"><i>See more</i></div>
									<div class="fbest">
										<img src="@/assets/images/fico13.png" alt="">
									</div>
									<div class="fcoke_content">
												<div class="fcoke clearfix">
												<div class="ic"><img :src="Domain+productListData.logo" alt=""></div>
												<h3>{{productListData.title}}</h3>
											</div>
											<div class="del">
												{{productListData.subTitle}}
											</div>
									</div>
								</li>
							</ul>
						</div>
						<div class="fresetls wow fadeInUp" data-wow-delay="0.3s" data-wow-duration="1s">
							<ul class="clearfix">
								<li>
									<div class="frese clearfix">
										<dl class="clearfix">
											<dd class="freset"><a @click="resetLabel">Reset</a></dd>
											<dd class="fconfirm"><a @click="confirmLabel">Confirm</a></dd>
										</dl>
									</div>
									<!-- <div class="fpagels clearfix">
										<ul>
											<li><div class="prod"></div></li>
											<li class="on"><a href="#">1</a></li>
											<li><a href="#">2</a></li>
											<li><div class="next"></div></li>
										</ul>
									</div> -->
								</li>
							</ul>
						</div>
					</form>
				</div>
			</div>
		</div>
	</div>
  </div>
</template>

<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "swiper/dist/css/swiper.css";
import headerCom from '@/components/header'
import {  labelList, getUserByCode, labelProduct } from '@/api/index'
export default {
  name: 'Home',
  components: {
	swiper,
    swiperSlide,
	headerCom
  },
    data () {
      return {
		 swiperOption: {
			slidesPerView: 2, //
			centeredSlides: true, //
			loop: true, //
			observer:true,
			observeParents:true,
			speed:12000,
			autoplay: {
				delay: 0,
				stopOnLastSlide: false, //trueslideloop
				disableOnInteraction: false, //swiperautoplaytrue
			},
     	 },
		labelListData: [],//
		scrollTop: 0,
		chooseId: '',
		productListData: {}
      }
    },
	computed: {
		swiper() {
			return this.$refs.mySwiper.swiper;
		},
	},
    updated () {
    },
    mounted () {
      new this.$wow.WOW().init()
	// //   this.getLabelList()
	//   let code = this.getUrlName('code')
	// 	if(!code&&!localStorage.getItem('token')){
	// 			 window.location.replace('https://discord.com/api/oauth2/authorize?client_id=983333457579094056&redirect_uri=http%3A%2F%2Fdashboard.mentostelecom.com&response_type=code&scope=identify')	
	// 	}else if (code&&!localStorage.getItem('token')){
	// 		this.getUserByCodeFunc(code)
	// 	}else{
			
	// 	}
		// this.getLabelList()
		window.addEventListener('scroll',this.handleScroll)
    },
    methods: {
		// 
		getLabelList() {
          var that = this
          labelList({},(res) => {
			for(var i in res.result){
				res.result[i].choose  = false
			}
			this.labelListData = res.result
            }, (err) => { console.log(err) })
		},
		 getUrlName (name) {
     		 /* eslint-disable */
      			return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ''])[1].replace(/\+/g, '%20')) || null
		 },
		//  getUserByCodeFunc(code){
		// 	 getUserByCode({code:code},(res) => {
		// 		localStorage.setItem('userInfo',JSON.stringify(res.result))
		// 		this.$store.commit('setUserInfo',res.result)
		// 		localStorage.setItem('token',res.result.token)
		// 		this.getLabelList()
        //     }, (err) => { console.log(err) })
		//  },
		 // 
		 chooseLable (index){
			this.chooseId = ''
			this.labelListData[index].choose = !this.labelListData[index].choose
			for (var i in this.labelListData){
				if (this.labelListData[i].choose){
					this.chooseId+=this.labelListData[i].id+','
				}
			}
			this.chooseId = this.chooseId.substring(0, this.chooseId.lastIndexOf(','));
			this.getLabelProduct(this.chooseId)
		 },
		 	// 
		getLabelProduct (id){
          labelProduct({id: id},(res) => {
			this.productListData = res.result[0]
            }, (err) => { console.log(err) })
		},
		 // 
		 resetLabel(){
			for(var i in this.labelListData){
				this.labelListData[i].choose  = false
			}
		 },
		 // 
		 confirmLabel (){
			var data = ''
			for (var i in this.labelListData){
				if (this.labelListData[i].choose){
					data+=this.labelListData[i].id+','
				}
			}
			// product
			this.toProduct(data)
		 },
		 chooseMoreBtn (){
				this.toProduct(this.chooseId)
		 },
		 // product
		 toProduct (id){
			this.$router.push({ path: '/product', query: { id: id } })
		 },
		 handleScroll(){
			let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop // 
			this.scrollTop = scrollTop
			},
			scroll_pro (){
				document.querySelector("#fpickthe").scrollIntoView(true);
			}
    }	
}
</script>
<style scoped>
  .layout{
      background: url(../assets/images/fbj1.jpg) no-repeat center/cover;    
	  
  }
</style>