						<!-- <li><a href="#">All</a></li>
						<li class="on"><a href="#">Monthy Proxies</a></li>
						<li><a href="#">Daily Proxies</a></li>
						<li><a href="#">Gmail Proxies</a></li> -->
		
						<li v-for='(item, index) in productListData' :key="index" @click="chooseProductIndex(index)" :class="chooseIndex==index?'on':''">
							<a href="#">{{item.proxyCate.title}}</a>
						</li>
					</ul>
				</div>
				<div class="fneonswiper wow fadeInUp" data-wow-delay="0.3s" data-wow-duration="1s">
					
					<div class="fneonsw">
						 <swiper :options="swiperOption" ref="mySwiper" class="swiper-wrapper" v-if="!isLable">
							<swiper-slide class="fneonsw_slide" v-for="(item,index) in proxyList" :key="index" >
									<div  @click="openShowPayInfo(item)">
										<div class="ico"  >
											<img :src="Domain+item.logo" alt="">
										</div>
										<div class="text">
											<h2>{{item.title}}</h2>
											<p>{{item.subTitle}}</p>
										</div>
									</div>
							</swiper-slide>
         			 </swiper>
					  <swiper :options="swiperOption" ref="mySwiper" class="swiper-wrapper" v-else>
							<swiper-slide class="fneonsw_slide" v-for="(item,index) in productListData" :key="index" >
									<div  @click="openShowPayInfo(item)">
										<div class="ico"  >
											<img :src="Domain+item.logo" alt="">
										</div>
										<div class="text">
											<h2>{{item.title}}</h2>
											<p>{{item.subTitle}}</p>
										</div>
									</div>
							</swiper-slide>
         			 </swiper>
							<!-- <swiper-slide class="fneonsw_slide">
								<div  @click="openShowPayInfo">
									<div class="ico">
										<img src="@/assets/images/fico17.png" alt="">
									</div>
									<div class="text">
										<h2>Neon proxy</h2>
										<p>Description of product func-
										tionsdescription of produ-
										ct functions.</p>
									</div>
								</div>
							</swiper-slide> -->
					</div>
					<div class="fprod" slot="button-prev"></div>
					<div class="fnext" slot="button-next"></div>
					<div class="fspan" slot="pagination"></div>
				</div>
				<div class="falltext wow fadeInUp" data-wow-delay="0.3s" data-wow-duration="1s">
					ALL SALES ARE FINAL AND WE DO NOT OFFER REFUNDS.We do not change or edit any orders once payment has been processed.
					Please read all descriptions carefully prior to ordering. We can only guarantee that the IPs in question work and 
					connect to the Internet in general and the above mentioned are on a best case efforts as working sites are always
					 changing.
				</div>
			</div>
		</div>
		
	</div>
	
	<!--  -->
	<div class="fcokebj" v-show="showPayInfo||showPayBox||showPayCode" @click="closePay"></div>
	<div class="fcokeproxy fproxys" v-show="showPayInfo">
		<h2>{{productDel.title}}</h2>
		<div class="fcha" @click="closePay"></div>
		<div class="list">
			<ul>
				<li>
					<input type="number" class="confirm_input" v-model="productNum"/>
					<h3></h3>
					<h6>Quantity</h6>
				</li>
				<li>
					<h3><i>250</i></h3>
					<h6>Storage</h6>
				</li>
				<li>
					<h3><i>{{productDel.location}}</i></h3>
					<h6>Location</h6>
				</li>
			</ul>
		</div>
		<div class="text">
			{{productDel.desc}}
		</div>
		<div class="fmoney clearfix">
			<h3>${{productDel.price}}</h3>
			<a href="javascript: ;" class="purchase" @click="openShowPayBox">Purchase</a>
		</div>
	</div>
	<div class="fcokepay fproxys" v-show="showPayBox">
		<div class="log pay_logo">
			<img src="@/assets/images/logo.png" alt="">
		</div>
		<div class="fcha" @click="closePay"></div>
		<div class="coke">
			<h2>Order Details</h2>
			<div class="order_detail_text">
				<span>{{productDel.title}}</span>
				<span>${{productDel.price}}</span>
			</div>
			<div class="order_detail_coupon">
				<span>Coupon</span>
				<select>  
					<option value='' disabled selected style='display:none;' class="place_coupon">Why not choose coupons?</option>  
					<option value='0'>1</option>  
					<option value='1'>2</option>  
				</select>
			</div>
			<div class="order_detail_total">
				<span>ToTal</span>
				<span>${{(productDel.price*1000*productNum*1000)/1000000}}</span>
			</div>
			<!-- <div class="fapply">
				<input type="text" class="text" placeholder="Coupon Code">
				<a href="#" class="apply">APPLY</a>
			</div> -->
			<div class="choose_pay_type">
				<div class="pay_type_item">
					<img src="@/assets/images/order_choose_1.png" alt="">
					<span>Wechat Pay</span>
				</div>
				<div class="pay_type_item">
					<img src="@/assets/images/order_choose_2.png" alt="">
					<span>Ali Pay</span>
				</div>
				<div class="pay_type_item">
					<img src="@/assets/images/order_choose_2.png" alt="">
					<span>Stripe</span>
				</div>
			</div>
			<div class="pay_type_code">
				<img :src="payCodeImage" alt="">
				<span></span>
			</div>
		</div>
		<!-- <div class="money">
			<input type="checkbox" class="che" name="fm" id="fm1">
			<label for="fm1">I agree with the <i>Terms of Use</i></label>
		</div> -->
		<div  class="pay" @click="checkOutOrder">Check Out</div>
	</div>
	<!-- <div v-if="showPayCode" class="fproxys_code">
		<div class="pay_image_code_box">
			<img src="@/assets/images/close.png" alt="" class="pay_code_close" @click="hasPayOrder"/>
			<img :src="payCodeImage" class="pay_code"/>
		</div>
	</div> -->

  </div>
</template>

<script>
// @ is an alias to /src
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "swiper/dist/css/swiper.css";
  import headerCom from '@/components/header'
  import {  productList, labelProduct, createOrder } from '@/api/index'
export default {
  name: 'login',
  components: {
	swiper, 
	swiperSlide,
	headerCom
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    },
  },
    data () {
      return {
		 swiperOption: {
				loop: false,  // 
				autoplay: false,  // 
				slidesPerView: 3,  // 
				spaceBetween: 40,  // 
				initialSlide:0,  // 
				freeMode: false,
				// 
				pagination: {
					el: '.fneonswiper .fspan',
					clickable: true
				},
				// 
				navigation: {
				nextEl: ".fneonswiper .fnext",
				prevEl: ".fneonswiper .fprod",
				},
     	 },
		 showPayInfo: false, //
		 showPayBox: false, //
		 showPayCode: false,// 
		 productListData: [],//
		 chooseIndex: 0,//
		 productDel:{},
		 isLable: false,// 
		 payCodeImage: '',
		 scrollTop: 0,
		 proxyList: [],
		 showLoading: false,
		 productNum: 1
      }
    },
    updated () {
    },

	watch: {
		// 
		'$route' (to, from) { 
			if(to.query.id !== from.query.id){
				this.getLabelProduct(to.query.id)
				this.isLable = true
			}
			if(to.query.index !== from.query.index){
				this.chooseIndex = to.query.index
				this.proxyList = this.productListData[to.query.index].proxyList
			}
		}
	},
    mounted () {
      new this.$wow.WOW().init()
	  if (this.$route.query.id){
		this.getLabelProduct(this.$route.query.id)
		this.isLable = true
	  }else{
		if (this.$route.query.index){
			this.chooseIndex = this.$route.query.index
		}
	//    this.getProductList()
	   this.isLable = false
	  }
	  window.addEventListener('scroll',this.handleScroll)

    },
    methods: {
		//
		openShowPayInfo (item){
			this.showPayInfo = true
			this.productDel = item
		},
		// 
		openShowPayBox (){
			this.confirmOrder()
			this.showPayInfo = false
			this.showPayBox = true
		},
		//
		closePay (){
			this.showPayInfo = false
			this.showPayBox = false
			this.showPayCode = false
			this.showLoading = false
		},
		// 
		getProductList() {
          var that = this
          productList({},(res) => {
			this.productListData = res.result
			this.proxyList = res.result[0].proxyList
            }, (err) => { console.log(err) })
		},
		// 
		getLabelProduct (id){
          labelProduct({id: id},(res) => {
			this.productListData.push(res.result[0])
            }, (err) => { console.log(err) })
		},
		// 
		chooseProductIndex (index){
			this.chooseIndex = index
			this.proxyList = this.productListData[index].proxyList
		},
		// 
		confirmOrder (){
			this.showLoading = true
			var that = this
			createOrder({proxyId: this.productDel.id, payType: 'native', coupon: '',num: that.productNum},(res) => {
				this.payCodeImage = res.result.url
            }, (err) => { 
				that.showLoading = false
			})
		},
		// 
		checkOutOrder (){
			alert('')
		},
		 handleScroll(){
			let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop // 
			this.scrollTop = scrollTop
			}
    }
}
</script>
<style scoped>
  .layout{
      background: url(../assets/images/fbj1.jpg) no-repeat center/cover;    
  }
  .floginvia{
	background: url(../assets/images/fimg5.jpg) no-repeat center/cover;    
  }
  .fneonsw_slide{
	background: url(../assets/images/fimg3.jpg) no-repeat center/cover;   
	display: block;
	padding: 17px 10px 40px 20px;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	-o-box-sizing: border-box;
	box-sizing: border-box;
	border-radius: 18px;
    overflow: hidden; 
  }
</style>