<template>
<div class="header" :class="scrollTop>0?'on': ''">
			<div class="wrap clearfix">
				<a class="logo" @click="toUrl('/')"><img src="@/assets/images/logo.png" alt=""></a>
				<div class="rts clearfix">
					<div class="naver">
						<ul class="clearfix">
							<li :class="index==0?'on':''"><h2><a @click="toUrl('/')">Home</a></h2></li>
							<li :class="index==1?'on':''"  @mouseenter="onHover(1)" @mouseleave="onHover(0)">
								<h2 @click="toUrl('/product')"><a>Product<i></i></a></h2>
								<div class="nlis" :class="isHover==1?'hasHover':'noHover'" v-if="showHover">
									<dl>
										<dd  v-for="(item, index) in labelListData" :key="index"  @click="toProduct(index)"><a>{{item.proxyCate.title}}</a></dd> 
									</dl>
								</div>
							</li>
							<li :class="index==2?'on':''" @click="toUrl('/dashboard')"><h2><a>Dashboard</a></h2></li>
							<li :class="index==3?'on':''" @click="toUrl('/support')" @mouseenter="onHover2(1)" @mouseleave="onHover2(0)">
								<h2><a>Support<i></i></a></h2>
								<div class="nlis" :class="isHover2==1?'hasHover':'noHover'" v-if="showHover2">
									<dl>
										<dd><a>FAQ</a></dd>
										<dd><a>Contact</a></dd>
									</dl>
								</div>
							</li>
						</ul>
					</div>

					<div class="login_box" v-if="token==''||!token">
						<div class="login" @click="toUrl('/login')">
								<span>Login</span>
							</div>
					</div>
					<div class="login_box" @mouseenter="showLoginOut=true" @mouseleave="showLoginOut=false" v-else>
						<div class="login" v-if="!showLoginOut">
							<span>{{userInfo.username}}</span>
						</div>
						<div class="login_out_box" v-if="showLoginOut">
							<span>{{userInfo.username}}</span>
							<span @click="LogoutFunc">Logout</span>
						</div>
					</div>
				</div>
			</div>
		</div>
</template>
<script>
import { productList } from '@/api/index'
export default {
  props: {
    index: {
        type: Number,
        required: true, 
        default: 0
    },
	scrollTop:{
        type: Number,
        required: false, 
        default: 0
    }
  },
  data () {
      return {
		userInfo: this.$store.state.userInfo,
		isHover: 0,
		showHover: false,
		isHover2: false,
		showHover2: false,
		labelListData: [],
		token:localStorage.getItem('token'),
		showLoginOut: false,
      }
	},
	 watch: {
        '$store.state.userInfo'(e){
                this.userInfo = e
                localStorage.setItem('userInfo',JSON.stringify(e))
                this.$store.commit('setUserInfo',e)
        },
 	 },
	mounted(){
		this.getLabelList()
	},
	methods:{
		toUrl (url){
			 this.$router.replace({ path: url })
		},
		onHover (type){
			var that = this
			this.isHover = type
			if (type==1){
				this.showHover = true
			}else{
				setTimeout(function(){
					that.showHover = false
				},500)
			}
		},
		onHover2 (type){
			var that = this
			that.isHover2 = type
			if (type==1){
				that.showHover2 = true
			}else{
				setTimeout(function(){
					that.showHover2 = false
				},500)
			}
		},
		// 
		getLabelList() {
          var that = this
		  return
          productList({},(res) => {
			this.labelListData = res.result
            }, (err) => { console.log(err) })
		},
		 // product
		 toProduct (index){
			this.$router.replace({ path: '/product', query: { index: index } })
		 },
		 LogoutFunc (){
			  localStorage.setItem('userInfo',JSON.stringify({}))
              this.$store.commit('setUserInfo',{})
              localStorage.setItem('token','')
			   window.location.replace('https://discord.com/api/oauth2/authorize?client_id=983333457579094056&redirect_uri=http%3A%2F%2Fdashboard.mentostelecom.com&response_type=code&scope=identify')	
		 }

	}

}
</script>

